var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/1.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("La Jurisdicción Especial para la Paz (JEP) constituye uno de los desarrollos jurídicos más significativos en la historia reciente del constitucionalismo colombiano. Su creación no puede entenderse únicamente como una reforma judicial, sino como una manifestación del principio constitucional de paz, del deber estatal de adoptar medidas excepcionales para finalizar un conflicto armado de más de cinco décadas y como respuesta a la necesidad de consolidar un modelo de justicia transicional que garantice verdad, justicia y reparación para las víctimas (Bouvier, 2014).")]),_c('div',{staticClass:"bg-color-1 p-5",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto mb-3 mb-lg-0"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/2.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Creada en el marco del Acuerdo Final de Paz de 2016 entre el Estado colombiano y las FARC-EP, la JEP fue concebida como la columna vertebral del componente de justicia del Sistema Integral de Verdad, Justicia, Reparación y No Repetición. Este sistema interinstitucional busca satisfacer los derechos de las víctimas y garantizar una transición hacia una paz estable y duradera. En ese contexto, la JEP tiene como función principal el esclarecimiento de crímenes graves cometidos durante el conflicto armado, así como la implementación de medidas restaurativas que promuevan la reconciliación nacional.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElemen