exclusivamente sancionar, sino reconstruir el tejido social, contribuir a una narrativa común del conflicto, reconocer los sufrimientos de las víctimas, promover la verdad histórica y facilitar la reintegración de excombatientes. En su estructura constitucional, la JEP se sustenta en principios fundamentales que le otorgan legitimidad y la conectan con el constitucionalismo garantista colombiano.")]),_c('p',[_vm._v("Entre esos principios destacan:")]),_c('div',{staticClass:"bg-color-4 p-5",attrs:{"data-aos":"fade-left"}},[_c('ul',{staticClass:"lista-ul--color"},[_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Centralidad de las víctimas")])]),_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Proporcionalidad en las sanciones")])]),_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Derecho a la verdad y la reparación integral")])]),_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Garantía de no repetición")])]),_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Aplicación preferente del derecho internacional de los derechos humanos y del derecho internacional humanitario")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/5.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Estos principios le otorgan una dimensión supraconstitucional, al vincularla con el sistema jurídico internacional y los compromisos del Estado colombiano en materia de justicia transicional (Bouvier, 2014).")]),_c('div',{staticClass:"bg-color-2 p-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La Corte Constitucional, en la Sentencia C-674 de 2017, avaló la constitucionalidad de la JEP, subrayando su legitimidad como un mandato de paz de carácter constitucional. Reiteró que la justicia transicional es una forma legítima de asegurar los derechos de las víctimas y facilitar la transición pacífica, siempre que se respeten los estándares mínimos de justicia y se protejan la autonomía e independencia de esta jurisdicción.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/6.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Así, el estudio del origen y fundamentos constitucionales de la JEP revela cómo el derecho puede ser herramienta de reconciliación, y no solo de control y sanción. Lejos de romper con el constitucionalismo clásico, la JEP representa una evolución del concepto de justicia constitucional, que reconoce las limitaciones del modelo punitivo tradicional y explora nuevas formas de resolución de conflictos centradas en la dignidad humana, el reconocimiento del otro y la transformación social (Bouvier, 2014).")]),_c('div',{staticClass:"bg-color-9 p-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("En definitiva, la existencia de la JEP plantea preguntas críticas sobre la relación entre justicia y paz, los límites de la penalización en contextos de conflicto, y el papel del derecho en la construcción de memoria colectiva. Abordar estas preguntas requiere un análisis profundo de su marco constitucional, principios fundantes, estructura normativa y tensiones con la justicia ordinaria. En las próximas secciones se desarrollarán estos elementos con mayor detalle para evaluar su impacto en la transición hacia la paz, el Estado Social de Derecho y los principios de justicia material.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_1","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("1.1")]),_vm._v(" Origen y naturaleza jurídica de la JEP")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('figure',[_c('div',{staticClass:"video"},[_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/9cqLwqWDCzs?si=F1b2M6SlLRnGqFf9","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/7.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Otra característica esencial es su autonomía. La JEP no depende de la Rama Judicial ni de ningún órgano del poder público. Tiene independencia funcional, administrativa y presupuestal, lo cual permite decisiones sin subordinación jerárquica y la adopción de procedimientos propios. Esta autonomía es clave para generar confianza entre víctimas, comparecientes y sociedad, especialmente en un país donde la desconfianza hacia la justicia ha sido histórica (Fjeld, Paredes & Paredes, 2016).")]),_c('div',{staticClass:"bg-color-7 p-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Además, su naturaleza jurídica se define por un doble anclaje en el derecho constitucional y en el derecho internacional. La JEP debe aplicar la Constitución de Colombia y respetar los tratados internacionales ratificados por el país, que forman parte del bloque de constitucionalidad. Esto le impide conceder amnistías o indultos por crímenes de guerra, de lesa humanidad o violaciones graves a los derechos humanos, conforme al Estatuto de Roma y la jurisprudencia de la Corte Interamericana de Derechos Humanos.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-6"},[_c('div',{staticClass:"px-4 px-md-5"},[_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/8.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Una de las tensiones más relevantes ha sido el tratamiento penal diferenciado para agentes del Estado. Mientras el Acuerdo de Paz prevé beneficios para ellos, se ha cuestionado si eso implica impunidad frente a los excombatientes de las FARC. La Corte Constitucional ha subrayado que el juzgamiento debe respetar los principios de verdad, reparación y proporcionalidad, garantizando derechos de las víctimas sin beneficios excesivos o discriminación arbitraria.")]),_c('div',{staticClass:"bg-color-1 p-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Desde la perspectiva institucional, la JEP cuenta con órganos judiciales y administrativos seleccionados mediante convocatoria internacional, garantizando transparencia, meritocracia y diversidad. La Comisión de Selección estuvo compuesta por expertos nacionales e internacionales. Este modelo, inédito en Colombia, buscó evitar injerencias políticas y asegurar legitimidad. Además, se promovió una composición plural con enfoque de género, étnico y regional (Fjeld, Paredes & Paredes, 2016).")])])])]),_c('p',[_vm._v("La competencia de la JEP se restringe a los crímenes más graves y representativos. Su objetivo no es judicializar masivamente, sino concentrarse en macrocasos que revelen patrones de criminalidad, estructuras de responsabilidad y contribuyan a la verdad. Este enfoque ha sido respaldado por organismos como la Oficina del Alto Comisionado de Naciones Unidas para los Derechos Humanos.")]),_c('p',{staticClass:"mb-5"},[_vm._v("Ejemplos de esta lógica son:")]),_c('div',{staticClass:"row justify-content-center align-items-stretch mb-5"},[_c('div',{staticClass:"col-lg-6 mb-4",attrs:{"data-aos":"zoom-in-up"}},[_c('div',{staticClass:"custom-image-card-2 h-100"},[_c('img',{staticClass:"custom-image-card__image",attrs:{"src":require("@/assets/curso/tema1/9.png"),"alt":""}}),_c('div',{staticClass:"custom-image-card__text p-4"},[_c('h5',{staticClass:"mb-2 text-center"},[_vm._v("Caso 01")]),_c('p',{staticClass:"mb-0"},[_vm._v("Secuestros por las FARC. Se documentaron miles de hechos, se tomaron versiones de excombatientes y se establecieron responsabilidades dentro de la estructura guerrillera.")])])])]),_c('div',{staticClass:"col-lg-6 mb-4",attrs:{"data-aos":"zoom-in-down"}},[_c('div',{staticClass:"custom-image-card-2 h-100"},[_c('img',{staticClass:"custom-image-card__image",attrs:{"src":require("@/assets/curso/tema1/10.png"),"alt":""}}),_c('div',{staticClass:"custom-image-card__text p-4"},[_c('h5',{staticClass:"mb-2 text-center"},[_vm._v("Caso 03")]),_c('p',{staticClass:"mb-0"},[_vm._v("Falsos positivos. Se han citado a altos mandos del Ejército Nacional para rendir versión sobre ejecuciones extrajudiciales presentadas como muertes en combate.")])])])])]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/11.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Estos procesos reflejan la función transformadora de la JEP, su aporte a la verdad histórica y su rol en la construcción de memoria.")]),_c('div',{staticClass:"bg-color-4 p-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La JEP no opera de manera aislada. Se articula con otros mecanismos como la Comisión para el Esclarecimiento de la Verdad y la Unidad de Búsqueda de Personas dadas por Desaparecidas. Juntas, estas instituciones conforman el Sistema Integral, que adopta una visión holística de justicia. No se limita a imponer sanciones, sino que también busca esclarecer la verdad, reparar a las víctimas y reconstruir el tejido social.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_2","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("1.2")]),_vm._v(" Principios constitucionales e internacionales aplicables")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/12.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("La Jurisdicción Especial para la Paz (JEP), como componente central del Sistema Integral de Verdad, Justicia, Reparación y No Repetición, opera sobre una sólida base de principios constitucionales y normas internacionales que le otorgan legitimidad, coherencia normativa y fundamento jurídico. Estos principios no solo orientan su funcionamiento interno, sino que permiten evaluar la compatibilidad de sus decisiones con el Estado Social de Derecho colombiano y con los compromisos internacionales asumidos por el país en materia de derechos humanos, justicia transicional y derecho internacional humanitario.")]),_c('div',{staticClass:"bg-color-2 p-5",attrs:{"data-aos":"fade-left"}},[_c('div',{static